<template>
  <div class="qrcode">
    <h2></h2>
    <div class="qrcode-cont">
      <textarea class="qrcode-cont-textarea" v-model="content" placeholder=","></textarea>
      <div class="qrcode-cont-option">
        <div class="qrcode-cont-option-cont">
          <label>
            :
            <input v-model="size" placeholder="200">
          </label>

          <label>
            :
            <input v-model="color" placeholder="#000000">
          </label>

          <label>
            :
            <input v-model="bgColor" placeholder="#ffffff">
          </label>
        </div>
        <span class="qrcode-cont-btn" @click="generateQRCode"></span>
      </div>
      <div style="text-align: center;">
        <canvas id="qrcode"></canvas>
      </div>
    </div>
   
  </div>
</template>

<script>

import QRCode from 'qrcode'

export default {
  name: 'Qrcode',
  data(){
    return {
      content:'',
      size:'',
      color:'',  // 
      bgColor:''  // 
    }
  },
  mounted(){
    
  },
  methods:{

    generateQRCode(){
      if(!this.content){
        return alert('')
      }
      let qrcode = document.querySelector('#qrcode')
      QRCode.toCanvas(qrcode, this.content, {
        margin:2,
        scale:5,
        maskPattern:8,
        width:this.size || 200,
        height:this.size || 200,
        color:{
          dark: this.color || '#000000',
          light: this.bgColor || '#ffffff'
        },
        toSJISFunc: QRCode.toSJIS ,
      }, error => {
        if(error){
          console.error(error)
        }
      })
    }


  }

}
</script>
